}
.RSPBprogressBar .RSPBprogression {
  position: absolute;
  -webkit-transition: width 0.3s ease;
  -o-transition: width 0.3s ease;
  transition: width 0.3s ease;
  left: 0;
  top: 0;
  bottom: 0;
  border-radius: 10px;
  background:#d60606 !important;
  z-index: -1;
}
